import { request } from "./request";
import Vue from 'vue'

export function failureAnalysis() {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvStationTwoAction/failureAnalysis?areaId=" + sessionStorage.getItem('companyId'),
        method: 'POST'
    })
}
export function failureAnalysisChart(abnormalAnalyseId) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvStationTwoAction/failureAnalysisChart?abnormalAnalyseId=" +abnormalAnalyseId,
        method: 'POST'
    })
}